.container {
  position: relative;
  width: 100%;
  height: 100%;
  border: 1px solid #ccc;
  margin: 0 auto;
  background-color: white;
}

.car {
  position: absolute;
  bottom: 10px;
  width: 80px;
  height: 260px;
  background: url(https://w7.pngwing.com/pngs/563/650/png-transparent-sports-car-bird-s-eye-view-race-car-car-color-vehicle.png) center/contain no-repeat;
}

.obstacle {
  position: absolute;
  width: 100px;
  height: 20px;
  background-color: #000;
}

.score {
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 18px;
  font-weight: bold;
}

.gameOver {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 24px;
  font-weight: bold;
}
