.pages {
    display: flex;
    justify-content: flex-start;
    margin: 0 auto;

    color: var(--color-3);
    font-size: 24px;
    gap: 20px;
    /* max-width: 700px; */
    /* overflow: hidden; */
    /* скрыть все, что выходит за границы блока */
    /* overflow-x: scroll; */
    /* text-overflow: ellipsis; */
    /* обрезать текст и добавить многоточие в конце */
    /* white-space: nowrap; */
    /* запретить перенос строк */
    grid-area: pa;
}

.pages span:hover {
    color: gray;
}

.selectedPage {
    font-weight: bold;
    color: rgb(141, 31, 31);
}

.pages button {
    width: 20px;
    height: 30px;
    background: none;
    border: none;
    margin: auto 0;
}

.btn_prev {
    transform: rotate(180deg);
}